.recent-block {
  width: 100%;
  height: 65vh;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
}
.recent-block-img {
  height: 45%;
  width: 45%;
  float: left;
}
.recent-block-text {
  /* margin-top: 10%; */
  width: 30%;
  float: right;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  height: 45%;
}
.recent-img1 {
  position: absolute;
  height: 30vh;
  transform: rotate(15deg);
}
.recent-img2 {
  height: 30vh;
  position: absolute;
  transform: rotate(-15deg);
}
.recent-img3 {
  height: 30vh;
  position: absolute;
  transform: rotate(-0deg);
}
.home {
  display: block;
}

.about-me {
  justify-content: center;
  align-items: center;
  align-self: center;
}
.about-header {
  display: flex;
  justify-self: center;
  align-self: center;
  justify-content: center;
  align-items: center;
}
.about-block {
  height: 100%;
  display: flex;
  justify-content: center;
  border-width: 5px solid black;
  padding: 24px;
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@media only screen and (max-width: 1024px) {
  .recent-block {
    padding-top: 45px;
    height: 80vh;
    flex-direction: column;
    display: flex;
  }
  .recent-block-img {
    width: 100%;
    height: 45%;
  }
  .recent-block-text {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 80%;
    text-align: center;
    float: right;
    height: 55%;
  }
  .about-block {
    border-width: 0px solid black;
    /* padding: 24px; */
    height: 100%;
    /* flex: 1; */
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .about-me {
    height: 55%;
    width: 90%;
    justify-content: center;
    align-items: center;
  }
  .recent-img3 {
    width: 100%;
    position: absolute;
    height: 25vh;
    margin-top: 10px;
    z-index: -1;
    transform: rotate(0deg);
  }
  .recent-img2 {
    width: 100%;
    position: absolute;
    height: 35vh;
    z-index: -2;
    transform: rotate(0deg);
  }
  .recent-img1 {
    width: 100%;
    z-index: -3;
    position: absolute;
    height: 45vh;
    transform: rotate(0deg);
  }
  .home {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    text-align: center;
    float: right;
    height: 55%;
  }
}
