.post {
  width: 30%;
  margin: 0px 10px 3.4% 10px;
  -webkit-box-shadow: 10px 10px 16px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 16px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 16px 0px rgba(0, 0, 0, 0.75);
  border-radius: 7px;
  overflow: hidden;
}

.postImg {
  width: 100%;
  height: 280px;
  object-fit: cover;
  /* border-radius: 7px; */
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
}

.postInfo {
  padding: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.postCat {
  font-family: "Varela Round", sans-serif;
  font-size: 11px;
  color: #be9656;
  line-height: 20px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}

.postTitle {
  font-family: "Josefin Sans", sans-serif;
  font-size: 24px;
  font-weight: 700;
  margin-top: 15px;
  cursor: pointer;
}
.postTitle:hover {
  color: #be9656;
}
.postDate {
  font-family: "Lora", serif;
  font-style: italic;
  font-size: 13px;
  color: #999;
  margin-top: 15px;
}

.postDesc {
  height: 250px;
  margin-bottom: 20px;
  padding: 8px;
  font-family: "Varela Round", sans-serif;
  text-align: justify;
  font-size: 14px;
  color: #444;
  line-height: 24px;
  margin-top: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}
@media only screen and (max-width: 1024px) {

  .post {
    width: 95%;
    margin: 0px 10px 3.4% 10px;
    -webkit-box-shadow: 10px 10px 16px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 10px 10px 16px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 10px 10px 16px 0px rgba(0, 0, 0, 0.75);
    border-radius: 7px;
    overflow: hidden;
  }

}