.intro {
  height: 45vh;
  background-color: white;
}
.i-left {
  float: left;
  width: 45%;
  max-height: 45vh;
  min-height: 40vh;
  display: block;
  position: relative;
  /* padding: 24px; */
  flex: 1;
  justify-content: center;
  flex-direction: column;
}
.i-left > :nth-child(1) {
  z-index: 2;
  position: absolute;
  bottom: 0;
  right: 0;
  transform: scale(1.69);
}
.i-name {
  z-index: 3;
  /* display: flex; */
  justify-content: space-between;
  flex-direction: column;
  gap: 0.25rem;
}
.i-name > :nth-child(1) {
  color: var(--black);
  font-weight: bold;
  font-size: 2rem;
}

.i-right {
  max-height: 40vh;
  overflow: hidden;
  border-left: 2rem solid white;
  border-top-left-radius: 15rem 616px;
  border-bottom-left-radius: 15rem 616px;
  position: relative;
  /* height: 45vh; */
  /* width: 100%; */
  /* resize: ; */
  z-index: 1;
}

@media only screen and (max-width: 700px) {
  .intro {
    height: 80vh;
    /* background-color: red; */
    /* display: block; */
    /* flex-direction: column; */
  }
  .i-left {
    float: left;
    width: 100%;
    height: 45%;
    padding: 24px;
    justify-content: center;
  }
  .i-left > :nth-child(1) {
    z-index: 1;
    position: absolute;
    bottom: 0;
    right: 0;
    transform: scale(1.69);
  }
  .i-name {
    z-index: 3;
    gap: 0.25rem;
  }
  .i-name > :nth-child(1) {
    color: var(--black);
    font-weight: bold;
    font-size: 2rem;
  }

  .i-right {
    /* float: right; */
    width: 100%;
    border: 0;
    overflow: hidden;
    border-left: 0 solid white;
    border-top-left-radius: 0 0;
    border-bottom-left-radius: 0 0;
    /* position: relative; */
    justify-content: center;
    align-items: center;
    height: 45%;
    z-index: 1;
  }
}
