.Footer {
  background-color: slateblue;
  display: flex;
  flex-direction: row;
  height: 100px;
  color: white;
  font-weight: bold;
  /* justify-content: flex-start; */
  justify-content: space-between;
  align-items: center;
  padding: 24px 24px 24px 24px;
}
.column-1 {
  justify-content: space-between;
  min-height: 100px;
  line-height: 50px;
}
.column-2 {
  justify-content: space-between;
  height: 100px;
  line-height: 50px;
}
.column-3 {
  justify-content: space-between;
  height: 100px;
  line-height: 50px;
}
.bottom {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-bottom: 24px;
}
.bottom * {
  padding: 0px 24px 0 0;
}
.bottom > input {
  height: 40px;
  /* border: 6px solid black; */
  border-radius: 6px;
}

@media only screen and (max-width: 1024px) {
  .Footer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    flex: 1;
    font-weight: bold;
    justify-content: space-between;
    padding: 24px 24px 24px 24px;
  }
  .column-1 {
    width: 100%;
  }
  .column-2 {
    width: 50%;
  }
  .column-3 {
    line-height: 1;
    flex: 1;
    float: right;
    width: 50%;
  }
  .column-3 *{
    height: 30%;
  }
  .bottom{

  }
}
