.top {
  /* width: 100%; */
  height: 50px;
  background-color: white;
  position: sticky;
  top: 0;
  padding-inline-start: 32px;
  padding-inline-end: 32px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: "Josefin Sans", sans-serif;
  z-index: 999;
}
.link > img {
  width: 25px;
  height: 25px;
}

.topLeft,
.topRight {
  /* flex: 3; */
  display: flex;
  align-items: center;
  justify-content: center;
}

.topCenter {
  flex: 1;
}

.topIcon {
  font-size: 20px;

  margin-right: 10px;
  color: #444;
  cursor: pointer;
}
.topIcon > img {
  width: 20px;
  height: 20px;
}

.topImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}

.topList {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.topListItem {
  margin-right: 20px;
  font-size: 18px;
  font-weight: 300;
  cursor: pointer;
}

.topSearchIcon {
  font-size: 18px;
  color: #666;
  cursor: pointer;
  margin-left: 15px;
}
